/*- Card container -*/
.card-container {
  position: relative;
  z-index: 10;
  margin: 32px auto;
  height: auto;
  -webkit-perspective: 1000px;
          perspective: 1000px;
 }
 
 img {
  max-width: 100%;
  max-height: 100%;
 }
 
 /*- Card body -*/
 .card-body {
  width: 100%;
  height: 100%;
  margin: 2rem;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all .7s linear;
 }
 
 /*- Flipping the card -*/
 .card-container:hover .card-body {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
 }
 
 .card-container:hover > .card-body > .side-front {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-in, visibility .75s linear;
 }
 
 /*- Card sides -*/
 .card-side {
  position: absolute;
  top: 0;
  width: 100%;
  color: #212121;
  background-color: #fff;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-shadow: 0 10px 35px rgba(50,50,93,.1),0 2px 15px rgba(0,0,0,.07);
 }
 
 /*- Frontside -*/
 .side-front [class^=col-xs]:first-of-type {
  padding-left: 0;
 }
 
 .side-front-content {
  padding-top: 32px;
  padding-right: 32px;
  padding-bottom: 32px;
 }
 
 /*- Backside -*/
 .side-back {
  z-index: 2;
  padding: 32px;
  text-align: center;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
 }
#root{
  display: flex;
  padding: 0;
  margin: 0;
}
.container{
  width: 100%;
}
.d-flex {
  display: flex;
  width: 100%;
}
.header {
  display: flex;
  width: inherit;
  list-style: none;
  justify-content: flex-end;
  padding: 1.5rem;
  padding-bottom: 0;
  margin: 0;
  background-color: #2c3e50;
  font-size: 1.25rem;
  z-index: 1;
  position: relative;
}
.header li {
  margin: 1rem;
}
.header li a, .social-footer li a, .header li a:visited {
  text-decoration: none;
  color: white;
}
.header li a:hover, .header li a.active{
  color: #00b5ad;
}
.content{
  background-color: #18bc9c;
  color: white;
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin-top: -3rem;
  z-index: 0;
  position: relative;
}
.content a { color: #2c3e50; }
.intro {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.intro-header {
  text-transform: uppercase;
}
.tagline{
  font-size: 1.25rem;
}
.about{
  padding: 1rem;
  font-size: 1.25rem;
  text-align: justify;
}
footer{
  background-color: #2c3e50;
  width: 100%;
  display: flex!important;
  justify-content: center;
  position: absolute;
  bottom: 0rem;
  padding: 1rem;
}
.social-footer {
  list-style: none;
  display: inline-flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.social-footer li {
  margin: 0 0.5rem;
}
.social-footer li a i{
  font-size: 1.35rem;
}

@media only screen and (max-width: 800px) {
  .tagline{
    font-size: 1rem!important;
  }
  .header {
    justify-content: center;
  }
  .about{
    font-size: 1rem;
  }
  .highlights{
    margin-top: 2rem;
  }
}
